<template>
    <div class="wrapper">
        <h1> : {{work.title}}</h1>
			<form @submit.prevent="update(work.id,item)">
				<div class="row">
					<div class="col-12 col-lg-6">
						<div class="form-group">
						    <label for="title"> </label>
						    <input type="text" v-model='item.title' class="form-control" id="title">
						</div>
					</div>
					<!-- /.col-12 col-lg-6 -->
					
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="cat">:</label>
					    	<select class="form-control" id="cat" v-model='item.cat'>
					    		<option disabled selected value=""> </option>
								<option value="sites"></option>
								<option value="personal"> </option>
							</select>
					  	</div> 
					</div>
					<!-- /.col-12 col-lg-6 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="client">:</label>
					    	<input type="text" v-model="item.client" class="form-control" id="client">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-4 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="link">  :</label>
					    	<input type="text" v-model="item.url" class="form-control" id="link">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-4 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="tech">:</label>
					    	<input type="text" v-model="item.tech" class="form-control" id="tech">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-4 -->
					<div class="col-12 col-lg-6">
						<div class="form-group">
					    	<label for="deadline"> :</label>
					    	<input type="text" v-model="item.deadline" class="form-control" id="deadline">
					  	</div>
					</div>
					<!-- /.col-12 col-lg-6 -->
					<div class="col-12">
						<div class="form-group">
					    	<label for="pre">' :</label>
					    	<PreviewImage @getImg='getSrc' :work='work'/>
					  	</div>
					</div>
					<!-- /.col-12 -->
					
				</div>
			  	<div class="form-group">
			    	<label for="describe"> :</label>
					<vue-editor for='describe' v-model="item.describe"></vue-editor>
			  	</div>
				
				<button class="btn btn-primary" type="submit"> </button>
			</form>	
			
    </div>
</template>
<script>
	import { db } from '@/main';

	import { VueEditor } from 'vue2-editor';
	import PreviewImage from '@/components/PreviewImage';
	export default {
		name: 'EditWork',
        props: ['work'],
		data() {
			return {
				item: {
					title: this.work.title,
					img: this.work.img,
					cat: this.work.cat,
					describe: this.work.describe,
					deadline: this.work.deadline,
					client: this.work.client,
					url: this.work.url,
					tech: this.work.tech
				},			
			}
		},
		components: {
		      VueEditor,
		      PreviewImage
		   },
		methods: {
		    update(id,item) {
                const createdAt = new Date();
		        db.collection('works').doc(id).update({ ...item, createdAt })
		   },
		    getSrc (data) {
		        this.item.img = data;
		    },
		 }
	}
</script>
<style scoped>
	input, select, textarea {
		display: block;
	}
</style>